  input[type="radio"] {
    position: absolute;
    opacity: 0;
    z-index: -1;
  }

  label {
    position: relative;
    text-align: center;
    display: flex;
    align-items: baseline;
    margin: 5px;
    line-height: 2;
    cursor: pointer;
    max-width: 10em;
    margin: 0 auto;
    border:none;
    margin-top: 15%;
  }
  label:before {
    border: 2px solid #e66368;
    border-radius: .25em;
    z-index: -1;
    }
  input[type="radio"] + label::before {
    border-radius: 1em;   
  }

  input[type="radio"]:checked + label {
    padding-left: 1em;
    background-color:#eb5a5ee0;
    box-shadow: 0 3.5px  rgb(66, 5, 5);
    color: #fff;
    border-radius: 30px;
  }
    label:before {
    background:#eb5a5ee0;
  }

  label,
  label::before {
    -webkit-transition: .35s all ease;
    transition: .35s all ease;
    font-size:110%;
    color: black;
  }

